events.on('ready', function() {
    loadTela();

    $('.btnA').on('click', function() {
        scorm.saveObject('btnA', true);
        navigate.goto('a-01');
    });
    $('.btnB').on('click', function() {
        scorm.saveObject('btnB', true);
        navigate.goto('b-01');
    });
    $('.btnC').on('click', function() {
        scorm.saveObject('btnC', true);
        navigate.goto('c-01');
    });
    $('.btnD').on('click', function() {
        scorm.saveObject('btnD', true);
        navigate.goto('d-01');
    });

    function loadTela() {
        if ( scorm.loadObject('btnA') == true ) {
            $('.btnA').removeClass('pulse')
        }
        if ( scorm.loadObject('btnB') == true ) {
            $('.btnB').removeClass('pulse')
        }
        if ( scorm.loadObject('btnC') == true ) {
            $('.btnC').removeClass('pulse')
        }
        if ( scorm.loadObject('btnD') == true ) {
            $('.btnD').removeClass('pulse')
        }
        if ( scorm.loadObject('btnA') == true  &&
             scorm.loadObject('btnB') == true  &&
             scorm.loadObject('btnC') == true  &&
             scorm.loadObject('btnD') == true ) {
                $('.arrow-right').removeClass('hide')
        }
    }
});